.App {
  font-family: "Tahoma";
}

.Nav-Bar {
  display: flex;
  position: sticky;
  justify-content: space-evenly;
  align-items: center;
  list-style-type: none;
  z-index: 1;
  top: 0px;
  opacity: 0.9;
  min-height: 3.5vh;
  margin: 0px;
  background: rgb(255, 140, 26);
}

.Nav-Kids {
  flex: 1;
  align-items: center;
  text-align: center;
}

.Nav-Kids>a {
color: white;
}

.Intro-Writing {
  position: absolute;
  text-align: center; 
  color: white;
  font-family: "Tahoma";
  font-style: italic;
  font-size: 25px;
  top: 35vh;
  left: 37vw;
}

.Background {
  position: relative;
  width: 100%;
  height: 100%;
}